.product-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.product-item {
  width: 100%;
  height: calc(100vh / 3);
  display: -ms-flexbox;
  display: flex;
  position: relative;
  cursor: pointer;
  overflow: hidden;
}
.product-item__name {
  -ms-flex-item-align: center;
      align-self: center;
  margin: 0 auto;
}

.product-item__add-to-cart {
  width: 100%;
  height: 50px;
  bottom: -50px;
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(255, 255, 255, 0.6);
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

.product-item:hover .product-item__add-to-cart  {
  bottom: 0;
  opacity: 1;
  visibility: visible;
}


